*,
::after,
::before {
  box-sizing: border-box;
}

body {
  background: $body-color;
  margin: 0;
  padding: 0;
  font-family: $primary-font;
  // @include desktop{
  //   overflow: hidden;
  // }
}

.container {
  // width: 100%;
  width: 1370px;
  margin: 0 auto;
  @include desktop-xl {
    width: 1140px;
  }
  @include desktop-lg {
    width: 960px;
  }
  @include desktop {
    width: 720px;
  }
  @include tablet {
    width: 540px;
  }
  @include mobile {
    width: 100%;
    padding-right: 20px;
    padding-left: 20px;
  }
}

.section-title {
  width: 70%;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 80px;
  @include tablet{
    margin-bottom: 50px;
  }
  h2 {
    margin-bottom: 60px;
    position: relative;
    @include mobile{
      margin-bottom: 50px;
    }
    &::before {
      position: absolute;
      content: "";
      width: 100px;
      height: 3px;
      background: $text-color-dark;
      left: 50%;
      bottom: -28px;
      transform: translateX(-50%);
      @include mobile{
        bottom: -20px;
      }
    }
  }
}

.magnetic {
  position: relative;
  z-index: 1000;
  padding: 25px;
  margin: -25px;
  display: inline-block;
  @include mobile{
    display: block;
  }
  &:hover {
    .button {
      transform: scale(1.08);
      .button-area {
        span {
          transform: translateY(-150%) skewY(7deg);
          &::after {
            transform: skewY(-7deg);
          }
        }
      }
    }
  }
}

.follower-wrapper {
  opacity: 0;
  transition: opacity 0.5s $site-ease;
  .pointer {
    position: fixed;
    z-index: 1000000;
    width: 10px;
    height: 10px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: -2px 0 0 -2px;
    background: $black;
    border-radius: 80%;
    backface-visibility: hidden;
    pointer-events: none;
    backface-visibility: hidden;
    transform-origin: center center;
    transition: background 0.5s $site-ease;
  }
  &.active {
    opacity: 1;
  }
}

.headroom {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transition: all 0.5s ease-in-out;
}

.headroom--unpinned {
  top: -150px;
}

@media (max-width: 991px) {
  .headroom--unpinned {
    top: 0;
  }
}

.headroom--pinned {
  top: 0;
}

// [data-scrollbar] {
//   width: 100%;
//   height: 100vh;
//   overflow: hidden;
// }

@keyframes up-down-animation {
  0% {
    transform: translate(-50%, 0);
  }
  100% {
    transform: translate(-50%, -10px);
  }
}

@keyframes opacity-animation {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: scale(1) rotate(180deg);
  }
}
