.button {
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  color: $text-color-dark;
  font-weight: 600;
  text-align: center;
  background: $primary-color;
  padding: 19px 50px;
  letter-spacing: 1px;
  border-radius: 10px;
  white-space: nowrap;
  cursor: pointer;
  transition: 0.7s $site-ease;
  @include mobile{
    font-size: 15px;
    display: block;
    width: 85%;
    margin: 0 auto;
  }
  .button-area {
    display: block;
    overflow: hidden;
    span {
      position: relative;
      display: block;
      padding: 2px 0;
      transition: transform 0.9s cubic-bezier(0.19, 1, 0.22, 1);
      transform-origin: left center;
      @include mobile{
        font-size: 15px;
      }
      &::after {
        content: attr(data-text);
        position: absolute;
        top: 150%;
        left: 0;
        transform: skewY(30deg);
        transform-origin: left center;
        transition: transform 0.9s cubic-bezier(0.19, 1, 0.22, 1);
      }
    }
  }
  &:visited {
    .button-area {
      span {
        transform: translateY(-150%) skewY(7deg);
        &::after {
          transform: skewY(-7deg);
        }
      }
    }
  }
}
