.nav {
    padding: 13px 30px;
    background: $white;
    display: flex;
    align-items: center;
    border-radius: 0 0 20px 20px;
    position: fixed;
    width: 100%;
    z-index: 1001;
	box-shadow: 0 16px 24px rgba(14, 13, 18, 0.04);
	@include desktop{
        border-radius: 0 0 10px 10px;
    }
    &-brand {
        flex: 1;
        a{
            img{
                @include mobile{
                    width: 70%;
                }
            }
        }
    }
    &-bar {
        margin: 0;
        padding: 0;
		list-style: none;
		@include desktop{
			display: none;
		}
        &-item {
            display: inline-block;
            a {
                padding: 15px 18px;
                font-size: 15px;
                text-transform: uppercase;
                font-weight: 600;
                color: $text-color-dark;
                text-decoration: none;
                display: block;
                position: relative;
            }
            &:not(:last-child){
                a{
                    &::before{
                        position: absolute;
                        content: '';
                        bottom: 10px;
                        left: 50%;
                        transform: translateX(-50%) scale(0);
                        width: 75%;
                        height: 2px;
                        background: $primary-color;
                        transform-origin: center center;
                        transition: transform 0.3s ease;
                    }
                    &:hover{
                        &::before{
                            transform: translateX(-50%) scale(1);
                        }
                    }
                    &.active{
                        &::before{
                            transform: translateX(-50%) scale(1);
                        }
                    }
                }
            }
            &:last-child {
                margin-left: 20px;
                a {
                    background: $primary-color;
                    padding: 15px 30px;
                    border-radius: 10px;
                }
            }
        }
    }
    // &.nav-bg {
    //   box-shadow: 0 16px 24px rgba(14, 13, 18, 0.07);
    // }
    .offcanvas-toggle {
        margin-left: 15px;
        padding: 17px 15px;
        border: 1px solid $primary-color;
		border-radius: 4px;
		display: none;
		@include desktop{
			display: block;
		}
        .icon-bar {
            height: 2px;
            width: 25px;
            transition: all 0.2s;
            background: $primary-color;
            display: block;
            &:not(:last-child) {
                margin-bottom: 5px;
            }
        }
    }
}
.offcanvas {
    width: 350px;
    min-height: 100%;
    position: fixed;
    background: #fff;
    z-index: 10000;
    right: -360px;
    padding: 80px 30px;
    transition: .3s cubic-bezier(0.645, 0.045, 0.355, 1);
    display: none;
	overflow-y: auto;
	@include desktop{
		display: block;
	}
	&.open{
		right: 0;
	}
	.offcanvas-close {
		width: 40px;
		height: 40px;
		position: absolute;
		border: 1px solid $primary-color;
		top: 30px;
		right: 30px;
		border-radius: 4px;
		cursor: pointer;
		&::before, &:after{
			position: absolute;
			content: "";
			width: 1px;
			height: 25px;
			background: $primary-color;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		&::before{
			transform: translate(-50%, -50%) rotate(-45deg);
		}
		&::after{
			transform: translate(-50%, -50%) rotate(45deg);
		}
	}
	&-menu{
        margin: 0;
        padding: 0;
		list-style: none;
		position: relative;
		li {
            display: block;
            a {
                padding: 15px 18px;
                font-size: 15px;
                text-transform: uppercase;
                font-weight: 600;
                color: $text-color-dark;
                text-decoration: none;
                display: block;
                &.active {
                    background: $primary-color;
                    padding: 15px 30px;
                    border-radius: 10px;
                    margin-left: -15px;
                }
            }
        }
	}
}
.offcanvas-overlay {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.2);
    z-index: 1999;
    transition: .3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    visibility: hidden;
    opacity: 0;
	// display: none;
	&.on {
		opacity: 1;
		visibility: visible;
	}
}
