.hero {
  min-height: 85vh;
  display: flex;
  align-items: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right center;
  border-radius: 0 0 50px 50px;
  overflow: hidden;
  padding: 220px 0 130px;
  @include desktop{
    background-position: center center;
    border-radius: 0 0 20px 20px;
  }
  @include mobile{
    min-height: 80vh;
    padding: 150px 0 100px;
  }
  .container {
    position: relative;
  }
  &-content {
    max-width: 632px;
    margin-left: 100px;
    position: relative;
    @include desktop{
      margin: 0;
      text-align: center;
      width: 100%;
    }
    h1,
    p {
      color: $white;
    }
    h1 {
      margin-bottom: 25px;
      @include desktop{
        font-size: 48px;
        line-height: 58px;
      }
    }
    p {
      margin-bottom: 70px;
      @include mobile{
        margin-bottom: 35px;
      }
    }
  }
  &-triangles {
    width: 50%;
    height: 120%;
    position: absolute;
    right: 0;
    top: -10%;
    @include mobile{
      width: 90%;
      right: 10%;
    }
    img {
      transform-origin: center center;
      &:nth-child(1) {
        position: absolute;
        top: 0;
        left: 3%;
        animation-name: opacity-animation;
        animation-duration: 2.5s;
        animation-iteration-count: infinite;
        // animation-direction: alternate;
      }
      &:nth-child(2) {
        position: absolute;
        top: 0;
        right: 0;
        width: 21px;
        transform: rotate(10deg);
        animation-name: opacity-animation;
        animation-duration: 2.5s;
        animation-delay: -1s;
        animation-iteration-count: infinite;
      }
      &:nth-child(3) {
        position: absolute;
        bottom: 5%;
        left: 10%;
        width: 10px;
        transform: rotate(2deg);
        animation-name: opacity-animation;
        animation-duration: 2.5s;
        animation-delay: -1.5s;
        animation-iteration-count: infinite;
      }
      &:nth-child(4) {
        position: absolute;
        bottom: 0%;
        right: 10%;
        transform: rotate(10deg);
        animation-name: opacity-animation;
        animation-duration: 2s;
        animation-delay: -0.5s;
        animation-iteration-count: infinite;
      }
    }
  }
}
