@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@400;600;700&display=swap");

body {
  font-family: $primary-font;
  font-size: 15px;
  color: $text-color;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  color: $text-color-dark;
  margin: 0;
}

p {
  font-size: 20px;
  line-height: 31px;
  color: $text-color;
  margin: 0;
  @include mobile{
    font-size: 16px;
    line-height: 26px;
  }
}

a {
  color: $text-color;
}

h1 {
  font-weight: 600;
  font-size: 70px;
  line-height: 80px;
  @include desktop{
    font-size: 50px;
    line-height: 60px;
  }
  @include mobile{
    font-size: 30px;
    line-height: 40px;
  }
}

h2 {
  font-size: 55px;
  line-height: 70px;
  @include desktop{
    font-size: 40px;
    line-height: 50px;
  }
  @include mobile{
    font-size: 30px;
    line-height: 40px;
  }
}

h3 {
  font-size: 40px;
  line-height: 50px;
  @include desktop{
    font-size: 30px;
    line-height: 40px;
  }
  @include mobile{
    font-size: 16px;
    line-height: 30px;
  }
}

h4 {
  font-size: 25px;
}

h5 {
  font-size: 18px;
}

h6 {
  font-size: 15px;
  font-weight: 300;
}
